@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

.customer-dashboard {
  font-family: 'Poppins', sans-serif;
  background: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.dashboard-container {
  flex: 1;
  padding: 40px 0 0 0;
}

.dashboard-hero {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ccc;
  margin: 0 auto 32px auto;
  max-width: 1200px;
  height: 400px;
  border-radius: 8px;
  background: #fafafa;
  position: relative;
  overflow: hidden;
}
.dashboard-hero-arrow {
  font-size: 2.5rem;
  color: #fff;
  cursor: pointer;
  padding: 0 24px;
  user-select: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  transition: background 0.3s;
}

.dashboard-hero-arrow:hover {
  background: rgba(0, 0, 0, 0.5);
}

.dashboard-hero-arrow.left {
  left: 20px;
}

.dashboard-hero-arrow.right {
  right: 20px;
}
.dashboard-hero-image {
  margin: 0 32px;
  width: 100%;
  height: 300px;
  position: relative;
  overflow: hidden;
}

.svg-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.slide-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.slide-image.active {
  opacity: 1;
}

.slide-image svg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.dashboard-hero-text {
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  font-size: 1.5rem;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  z-index: 2;
  background: rgba(0, 0, 0, 0.3);
  padding: 10px 20px;
  border-radius: 4px;
  width: 80%;
}

.dashboard-hero-dots {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  z-index: 2;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  transition: background 0.3s;
}

.dot.active {
  background: #fff;
}

.dashboard-activities {
  text-align: center;
  margin: 40px 0 32px 0;
}
.dashboard-activities h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 8px;
}
.dashboard-activities p {
  color: #555;
  font-size: 1.1rem;
}

.dashboard-categories {
  text-align: center;
}
.dashboard-categories h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 24px;
}
.dashboard-categories-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
  max-width: 900px;
  margin: 0 auto;
}
.dashboard-category {
  border: 2px solid #ccc;
  border-radius: 10px;
  padding: 32px 0;
  background: #fff;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  transition: box-shadow 0.2s, border-color 0.2s;
}
.dashboard-category:hover {
  border-color: #000;
  box-shadow: 0 2px 12px rgba(0,0,0,0.09);
}

@media (max-width: 900px) {
  .dashboard-categories-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .dashboard-categories-grid {
    grid-template-columns: 1fr;
  }
  .dashboard-hero {
    flex-direction: column;
    min-height: 160px;
  }
  .dashboard-hero-arrow {
    padding: 0 10px;
  }
  .dashboard-hero-image {
    margin: 0 0 12px 0;
  }
}
