.login-choice-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  padding: 20px;
}

.login-choice-container h2 {
  margin-bottom: 30px;
  color: #333;
  font-size: 2rem;
}

.login-options {
  display: flex;
  gap: 20px;
}

.login-option button {
  padding: 15px 30px;
  font-size: 1.1rem;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.login-option button:hover {
  background-color: #0056b3;
}
